import React from "react";
import page_ from "./../fonction_js/page_info";
import { useEffect } from "react";
// import { Vue_ensembleList } from '../data/vueEnsembleList';

const FirstFondPage = ({ widthScreen, heightScreen, scrollPage, List }) => {
  function resizeImg() {
    if (page_.getXmax() > 600) {
      let img = document.getElementsByClassName("firstImagePage");
      for (let value of img) {
        value.style.height = `${page_.getYmax()}px`;
      }
    } else {
      let mobile = document.getElementsByClassName("mobile_background");
      for (let value of mobile) {
        value.style.height = `auto`;
      }
    }
  }

  function apparitionTitre() {
    let div_ = document.getElementsByClassName("firstImagePage");
    for (let value of div_) {
      let h = value.children;
      for (let value of h) {
        if (scrollPage > page_.getYmax() / 3) {
          value.style.display = "none";
        } else {
          value.style.display = "block";
        }
      }
    }
  }

  useEffect(() => {
    resizeImg();
  }, [widthScreen, heightScreen]);
  useEffect(() => {
    if (widthScreen > 600) {
      apparitionTitre();
    }
  }, [scrollPage]);

  function titre(value) {
    if (value === false) {
      return null;
    } else {
      return <h2>{value}</h2>;
    }
  }

  return (
    <React.Fragment>
      {List.map((value) =>
        widthScreen > 600 ? (
          <div
            key={value.classN}
            className={`firstImagePage ${value.classN}`}
            style={{ backgroundImage: `url(${value.coverBackground})` }}
          >
            <h1>{value.h1_}</h1>
            {titre(value.h2_)}
          </div>
        ) : (
          <div key={value.classN} className="mobile_background">
            <img src={value.coverImg} alt="un océan" />
            <h1>{value.h1_}</h1>
            {titre(value.h2_)}
          </div>
        )
      )}
    </React.Fragment>
  );
};

export default FirstFondPage;
