import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { afficheList } from "../../data/afficheList";
import page_ from "../fonction_js/page_info";

const AfficheComponent = ({ scrollPage }) => {
  useEffect(() => {
    let elements = document.getElementsByClassName("affiche_container");
    for (let value of elements) {
      page_.scrollFunctionElement(value, "apparitionAffiche", true, -100);
    }
  }, [scrollPage]);
  return (
    <div className="programme_affiche_container">
      {afficheList.map(({ cover, titre, description }, key) => (
        <div key={key} id={key.toString()} className="affiche_container">
          <div className="affiche">
            <Link to={`/programme/${key}`}>
              <img
                src={cover}
                alt={`Affiche de ${titre}`}
                className="pointer"
              />
            </Link>
          </div>
          {/* <div className="description">
            <h2>{titre}</h2>
            <p>{description}</p>
          </div> */}
        </div>
      ))}
    </div>
  );
};

export default AfficheComponent;
