import React from "react";
import facebook from "./../../assets/facebook-brands.svg";
import instagram from "./../../assets/instagram-brands.svg";

const Footer = () => {
  return (
    <footer>
      <h2>Nos réseaux</h2>
      <div className="linkcontainer">
        <a
          href="https://www.facebook.com/groups/195697227305179/user/100076836777981"
          target="_blank"
        >
          <img src={facebook} alt="logo facebook"></img>
        </a>
        <a
          href="https://www.instagram.com/liste_bde_elementaire/"
          target="_blank"
        >
          <img src={instagram} alt="logo instagram"></img>
        </a>
      </div>
    </footer>
  );
};

export default Footer;
