import React, { useEffect, useState } from "react";
// import { faqList } from "../../data/faqList";
import page_ from "../fonction_js/page_info";
import axios from "axios";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fas, faThumbsUp } from "@fortawesome/free-solid-svg-icons";

const PostComponent = ({ scrollPage, modifyFaqDb, setModifyFaqDb }) => {
  const [listOfFaq, setListOfFaq] = useState([]);
  // let navigate = useNavigate();
  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}api/PostIt`)
      .then((response) => {
        setListOfFaq(response.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [modifyFaqDb]);

  const deletePost = (id) => {
    axios
      .delete(`${process.env.REACT_APP_API_URL}api/PostIt/${id}`)
      .then((response) => {
        console.log("supprimé");
        setModifyFaqDb(!modifyFaqDb);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  function updapteLikes(like, id) {
    let thumb = document.getElementById(`thumb${id.toString()}`);
    let data = 0;
    // data = {
    //   Like: (like || 0) - 1,
    // };
    if (page_.readCookie(`Like${id.toString()}`) === "true") {
      data = {
        Like: (like || 0) - 1,
      };
      thumb.style.color = "black";
      page_.createCookie(`Like${id.toString()}`, "false", 365);
    } else {
      data = {
        Like: (like || 0) + 1,
      };
      thumb.style.color = "green";
      page_.createCookie(`Like${id.toString()}`, "true", 365);
    }

    axios
      .put(`${process.env.REACT_APP_API_URL}api/PostIt/${id}`, data)
      .then((response) => {
        console.log("modifié");
        setModifyFaqDb(!modifyFaqDb);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  useEffect(() => {
    let elements = document.getElementsByClassName("element_container");
    for (let value of elements) {
      page_.scrollFunctionElement(value, "apparitionFaq", true);
    }
  }, [scrollPage]);

  return (
    <div className="Postit_container">
      {listOfFaq
        .sort((a, b) => b.Like - a.Like)
        .map(({ Idee, Pseudo, id, Like }) => (
          <div key={id} className="element_container">
            <div className="question_container">
              <p>{Idee}</p>
            </div>
            <div className="response_container">
              <h3>Prénom :</h3>
              <p>{Pseudo}</p>
            </div>
            <div className="like_container">
              <p>{Like || 0}</p>
              <FontAwesomeIcon
                icon={faThumbsUp}
                className="pointer fa"
                id={`thumb${id.toString()}`}
                onClick={() => {
                  updapteLikes(Like, id);
                }}
                style={
                  page_.readCookie(`Like${id.toString()}`) === "true"
                    ? { color: "green" }
                    : { color: "black" }
                }
              />
            </div>
            {/* <button
            onClick={() => {
              let bol = window.confirm(
                "Voulez vous vraiment supprimer ce post ?"
              );
              if (bol) {
                deletePost(id);
              }
            }}
            className="pointer"
          >
            Supprimer
          </button> */}
          </div>
        ))}
    </div>
  );
};

export default PostComponent;
