import React, { useEffect } from "react";

import { BrowserRouter, Route, Routes } from "react-router-dom";

import Home from "./pages/Home";
import NotFound from "./pages/NotFound";

import { useState } from "react";
import page_ from "./components/fonction_js/page_info";
import Programme from "./pages/Programme";
import LiensUtiles from "./pages/LiensUtiles";
import Faq from "./pages/Faq";
import ImgComponent from "./components/programme/ImgComponent";
import FaqAdmin from "./pages/FaqAdmin";
import Mur from "./pages/Mur";
import axios from "axios";
import VideoExp from "./pages/VideoExp";

const App = () => {
  const [widthScreen, setwidthScreen] = useState(page_.getXmax());
  const [heightScreen, setHeightScreen] = useState(page_.getYmax());
  const [scrollPage, setScrollPage] = useState(page_.getScrollPage());
  const [boolScrollProgramme, setBoolScrollProgramme] = useState(false);
  const [idProgramme, setIdProgramme] = useState(0);
  const [boolMur, setBoolMur] = useState("true");
  const [boolProgramme, setBoolProgramme] = useState("true");

  window.onresize = function () {
    setwidthScreen(page_.getXmax());
    setHeightScreen(page_.getYmax());
  };
  window.addEventListener("scroll", function () {
    setScrollPage(page_.getScrollPage());
  });

  // useEffect(() => {
  //   axios
  //     .get(`${process.env.REACT_APP_API_URL}api/Mur/${1}`)
  //     .then((response) => {
  //       setBoolMur(response.data.bool);
  //     })
  //     .catch((err) => {
  //       console.log(err);
  //     });
  // }, []);

  return (
    <React.StrictMode>
      <BrowserRouter>
        <Routes>
          <Route
            exact
            path="/"
            element={
              <Home
                widthScreen={widthScreen}
                scrollPage={scrollPage}
                heightScreen={heightScreen}
                boolMur={boolMur}
              />
            }
          />
          <Route
            exact
            path="/programme"
            element={
              <Programme
                widthScreen={widthScreen}
                scrollPage={scrollPage}
                boolScrollProgramme={boolScrollProgramme}
                setBoolScrollProgramme={setBoolScrollProgramme}
                id={idProgramme}
                boolMur={boolMur}
                boolProgramme={boolProgramme}
                setBoolProgramme={setBoolProgramme}
              />
            }
          />
          <Route
            exact
            path="/revelation"
            element={
              <VideoExp
                widthScreen={widthScreen}
                scrollPage={scrollPage}
                boolMur={boolMur}
              />
            }
          />
          <Route
            exact
            path="/FAQ"
            element={<Faq widthScreen={widthScreen} scrollPage={scrollPage} />}
          />
          <Route
            exact
            path="/programme/:id"
            element={
              <ImgComponent
                widthScreen={widthScreen}
                heightScreen={heightScreen}
                setBoolScrollProgramme={setBoolScrollProgramme}
                setIdProgramme={setIdProgramme}
              />
            }
          />

          <Route
            exact
            path={`/faq/Admin`}
            element={
              <FaqAdmin
                widthScreen={widthScreen}
                scrollPage={scrollPage}
                boolMur={boolMur}
                setBoolMur={setBoolMur}
                boolProgramme={boolProgramme}
                setBoolProgramme={setBoolProgramme}
              />
            }
          />
          {boolMur === "true" && (
            <Route
              exact
              path="/Mur"
              element={
                <Mur
                  widthScreen={widthScreen}
                  scrollPage={scrollPage}
                  boolMur={boolMur}
                />
              }
            />
          )}

          <Route path="*" element={<NotFound />} />
        </Routes>
      </BrowserRouter>
    </React.StrictMode>
  );
};

export default App;

