import React, { useState } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import emailjs from "emailjs-com";

const FormulaireContact = () => {
  const [messageEnvoyer, setMessageEnvoyer] = useState(false);

  const initialValues = {
    email: "",
    message: "",
  };

  const validationSchema = Yup.object().shape({
    email: Yup.string().required("Email is a required field"),
    message: Yup.string().required("Message is a required field"),
  });

  const OnSubmit = (data, onSubmitProps) => {
    emailjs
      .sendForm(
        "service_zi82r5a",
        "template_4tf3enp",
        document.getElementById("form"),
        "user_3OZ89RWhkdGTF3x6hLaS5"
      )
      .then(
        (result) => {
          console.log(result.text);
          setMessageEnvoyer(true);
          onSubmitProps.resetForm();
          setTimeout(() => {
            setMessageEnvoyer(false);
          }, 3000);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={OnSubmit}
      validationSchema={validationSchema}
    >
      <Form id="form" className="formulaire_container">
        <h2>Des questions ? Des remarques ?</h2>

        <ErrorMessage name="email" component="span" />
        <Field
          name="email"
          type="email"
          className="input"
          placeholder="Email*"
        />

        <ErrorMessage name="message" component="span" />

        <Field
          name="message"
          className="input"
          component="textarea"
          rows={6}
          placeholder="Message*"
        />
        <button className="pointer" type="submit">
          Envoyer
        </button>
        {messageEnvoyer && (
          <div className="message_envoyerContainer">
            <p>Votre message a bien été envoyé !</p>
          </div>
        )}
      </Form>
    </Formik>
  );
};

export default FormulaireContact;
