import React, { useEffect, useState } from "react";
import Navigation from "../components/commun/Navigation";
import AfficheComponent from "../components/programme/AfficheComponent";
import IntroProgramme from "../components/programme/IntroProgramme";
import page_ from "./../components/fonction_js/page_info";
import Footer from "../components/commun/Footer";
import axios from "axios";

const Programme = ({
  widthScreen,
  scrollPage,
  boolScrollProgramme,
  setBoolScrollProgramme,
  id,
  boolMur,
  boolProgramme,
  setBoolProgramme,
}) => {
  useEffect(() => {
    if (boolScrollProgramme == true) {
      let element = document.getElementById(`${id.toString()}`);
      let scrollvalue = element.offsetTop;
      window.scrollTo(0, scrollvalue);
      setBoolScrollProgramme(false);
    } else {
      window.scrollTo(0, 0);
    }
  }, []);

  // useEffect(() => {
  //   axios
  //     .get(`${process.env.REACT_APP_API_URL}api/Mur/${2}`)
  //     .then((response) => {
  //       setBoolProgramme(response.data.bool);
  //     })
  //     .catch((err) => {
  //       console.log(err);
  //     });
  // }, []);
  return (
    <React.Fragment>
      <Navigation
        bol_nosfonds={false}
        bol_transparent={false}
        widthScreen={widthScreen}
        scrollPage={scrollPage}
        boolMur={boolMur}
      />
      <IntroProgramme boolProgramme={boolProgramme} />
      <AfficheComponent scrollPage={scrollPage} />
      <Footer />
    </React.Fragment>
  );
};

export default Programme;
