import React, { useEffect } from "react";
import Footer from "../components/commun/Footer";
import Navigation from "../components/commun/Navigation";
import Video from "../components/VideoExp/Video";

const VideoExp = ({ widthScreen, scrollPage, boolMur }) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <React.Fragment>
      <Navigation
        bol_nosfonds={false}
        bol_transparent={false}
        widthScreen={widthScreen}
        scrollPage={scrollPage}
        boolMur={boolMur}
      />
      <Video widthScreen={widthScreen} />
      <Footer />
    </React.Fragment>
  );
};

export default VideoExp;
