import React from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import axios from "axios";

const CreatePost = ({ setModifyFaqDb, modifyFaqDb }) => {
  const initialValues = {
    Idee: "",
    Pseudo: "",
  };

  const validationSchema = Yup.object().shape({
    Idee: Yup.string().required(),
    Pseudo: Yup.string().required(),
  });
  const OnSubmit = (data, onSubmitProps) => {
    axios
      .post(`${process.env.REACT_APP_API_URL}api/PostIt`, data)
      .then((response) => {
        console.log("créer", response);
        setModifyFaqDb(!modifyFaqDb);
        onSubmitProps.resetForm();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <React.Fragment>
      <div className="mur_titre">
        <h1>Le mur d'expressions</h1>
        <p>
          Ici vous pouvez vous exprimer, vous pouvez partager toutes vos idées
          et voter pour vos idées préférées !
        </p>
      </div>
      <div className="createFaq_container">
        <Formik
          initialValues={initialValues}
          onSubmit={OnSubmit}
          validationSchema={validationSchema}
        >
          <Form className="formContainer">
            <label>Idée :</label>
            <ErrorMessage name="Idee" component="span" />
            <Field
              className="inputCreatePost"
              name="Idee"
              component="textarea"
              rows="4"
              placeholder="Idée ..?"
            />
            <label>Prénom:</label>
            <ErrorMessage name="Pseudo" component="span" />
            <Field
              className="inputCreatePost"
              name="Pseudo"
              placeholder=" Prénom ...."
            />

            <button className="pointer" type="submit">
              Créer le post
            </button>
          </Form>
        </Formik>
      </div>
    </React.Fragment>
  );
};

export default CreatePost;
