import React, { useEffect, useState } from "react";
import Navigation from "../components/commun/Navigation";
import CreatePost from "../components/Mur/CreatePost";
import PostComponent from "../components/Mur/PostComponent";
import Footer from "../components/commun/Footer";

const Mur = ({ widthScreen, scrollPage, boolMur }) => {
  const [modifyFaqDb, setModifyFaqDb] = useState(false);
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <React.Fragment>
      <Navigation
        bol_nosfonds={false}
        bol_transparent={false}
        widthScreen={widthScreen}
        scrollPage={scrollPage}
        boolMur={boolMur}
      />
      <CreatePost setModifyFaqDb={setModifyFaqDb} modifyFaqDb={modifyFaqDb} />

      <PostComponent
        scrollPage={scrollPage}
        modifyFaqDb={modifyFaqDb}
        setModifyFaqDb={setModifyFaqDb}
      />
      <Footer />
    </React.Fragment>
  );
};

export default Mur;
