import React, { useEffect, useState } from "react";
import Navigation from "../components/commun/Navigation";
import CreateFaq from "../components/faqAdmin/CreateFaq";
import FaqComponentAdmin from "../components/faqAdmin/FaqComponentAmin";
import Footer from "../components/commun/Footer";
import MurActivaton from "../components/faqAdmin/MurActivaton";

const FaqAdmin = ({
  widthScreen,
  scrollPage,
  boolMur,
  setBoolMur,
  boolProgramme,
  setBoolProgramme,
}) => {
  const [modifyFaqDb, setModifyFaqDb] = useState(false);
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <React.Fragment>
      <Navigation
        bol_nosfonds={false}
        bol_transparent={false}
        widthScreen={widthScreen}
        scrollPage={scrollPage}
        boolMur={boolMur}
      />
      <CreateFaq setModifyFaqDb={setModifyFaqDb} modifyFaqDb={modifyFaqDb} />
      {/* <MurActivaton
        boolMur={boolMur}
        setBoolMur={setBoolMur}
        boolProgramme={boolProgramme}
        setBoolProgramme={setBoolProgramme}
      /> */}

      <FaqComponentAdmin
        scrollPage={scrollPage}
        modifyFaqDb={modifyFaqDb}
        setModifyFaqDb={setModifyFaqDb}
      />
      <Footer />
    </React.Fragment>
  );
};

export default FaqAdmin;
