import React, { useEffect } from "react";
import video1 from "./../../assets/video1.mp4";
import video2 from "./../../assets/video2.mp4";
import video3 from "./../../assets/video3.mp4";
import video4 from "./../../assets/video4.mp4";
import video5 from "./../../assets/video5.mp4";
import video6 from "./../../assets/video6.mp4";

const Video = ({ widthScreen }) => {
  useEffect(() => {
    let elements = document.querySelectorAll(".video_");
    let width = 0;
    if (widthScreen < 800) {
      width = widthScreen - 100;
    } else {
      width = widthScreen * 0.6;
    }
    for (let e of elements) {
      e.style.width = `${width}px`;
    }
  }, [widthScreen]);
  return (
    <div className="video_container">
      <h2>
        Voici des vidéos expliquant le pourquoi du comment de la liste
        Elémentaire.
      </h2>
      <video className="video_" controls muted>
        <source src={video1} type="video/mp4" />
      </video>
      <video className="video_" scontrols muted>
        <source src={video2} type="video/mp4" />
      </video>
      <video className="video_" controls muted>
        <source src={video3} type="video/mp4" />
      </video>
      <video className="video_" scontrols muted>
        <source src={video4} type="video/mp4" />
      </video>
      <video className="video_" controls muted>
        <source src={video5} type="video/mp4" />
      </video>
      <video className="video_" scontrols muted>
        <source src={video6} type="video/mp4" />
      </video>
    </div>
  );
};

export default Video;
