import React, { useEffect, useState } from "react";
// import { faqList } from "../../data/faqList";
import page_ from "../fonction_js/page_info";
import axios from "axios";

const FaqComponentAdmin = ({ scrollPage, modifyFaqDb, setModifyFaqDb }) => {
  const [listOfFaq, setListOfFaq] = useState([]);
  // let navigate = useNavigate();
  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}api/faq`)
      .then((response) => {
        setListOfFaq(response.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [modifyFaqDb]);

  const deletePost = (id) => {
    axios
      .delete(`${process.env.REACT_APP_API_URL}api/faq/${id}`)
      .then((response) => {
        console.log("supprimé");
        setModifyFaqDb(!modifyFaqDb);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    let elements = document.getElementsByClassName("element_container");
    for (let value of elements) {
      page_.scrollFunctionElement(value, "apparitionFaq", true);
    }
  }, [scrollPage]);

  return (
    <div className="faq_container_Admin">
      {listOfFaq.map(({ reponse, Question, id }) => (
        <div key={id} className="element_container">
          <div className="question_container">
            <p>{Question}</p>
          </div>
          <div className="response_container">
            <h3>Réponse :</h3>
            <p>{reponse}</p>
          </div>
          <button
            onClick={() => {
              let bol = window.confirm(
                "Voulez vous vraiment supprimer ce post ?"
              );
              if (bol) {
                deletePost(id);
              }
            }}
            className="pointer"
          >
            Supprimer
          </button>
        </div>
      ))}
    </div>
  );
};

export default FaqComponentAdmin;
