import React, { useEffect } from "react";
import { afficheList } from "../../data/afficheList";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router";

const ImgComponent = ({
  widthScreen,
  heightScreen,
  setBoolScrollProgramme,
  setIdProgramme,
}) => {
  const navigate = useNavigate();
  let { id } = useParams();
  const affiche = afficheList[id];
  useEffect(() => {
    let img = document.getElementById("imgAffiche");
    img.style.height = `${heightScreen}px`;
  }, [widthScreen, heightScreen]);

  const OnClick = () => {
    setBoolScrollProgramme(true);
    setIdProgramme(id);
    setTimeout(() => {
      navigate("/programme");
    }, 100);
  };

  return (
    <div id="imgAffiche" className="PageImg">
      <img src={affiche.cover} alt={`${affiche.titre} cover`} />
      <p className="pointer" onClick={OnClick}>
        Déccouvrir les autres affiches.
      </p>
    </div>
  );
};

export default ImgComponent;
