import React, { useEffect } from "react";
import Navigation from "../components/commun/Navigation";
import FaqComponent from "../components/faq/FaqComponent";
import FormulaireContact from "../components/faq/FormulaireContact";
import Footer from "../components/commun/Footer";

const Faq = ({ widthScreen, scrollPage, boolMur }) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <React.Fragment>
      <Navigation
        bol_nosfonds={false}
        bol_transparent={false}
        widthScreen={widthScreen}
        scrollPage={scrollPage}
        boolMur={boolMur}
      />
      <FormulaireContact />
      <FaqComponent scrollPage={scrollPage} />
      <Footer />
    </React.Fragment>
  );
};

export default Faq;
