import React, { useEffect } from "react";

import coverBackground from "./../assets/Londres_1.jpg";
import Navigation from "../components/commun/Navigation";
import Presentation from "../components/home/Presentation";
import FirstFondPage from "../components/commun/FirstFondPage";
import PresentationMembres from "../components/home/PresentationMembres";
import Footer from "../components/commun/Footer";

const Home = ({ widthScreen, scrollPage, heightScreen, boolMur }) => {
  const homeList = [
    {
      coverBackground: coverBackground,
      coverImg: "./img/Londres_1.jpg",
      classN: "Home_Page",
      h1_: "Élémentaire",
      h2_: false,
    },
  ];

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <React.Fragment>
      <Navigation
        bol_nosfonds={false}
        bol_transparent={true}
        widthScreen={widthScreen}
        scrollPage={scrollPage}
        boolMur={boolMur}
      />
      <FirstFondPage
        widthScreen={widthScreen}
        heightScreen={heightScreen}
        scrollPage={scrollPage}
        List={homeList}
      />
      <Presentation scrollPage={scrollPage} widthScreen={widthScreen} />

      <PresentationMembres
        scrollPage={scrollPage}
        widthScreen={widthScreen}
        heightScreen={heightScreen}
      />
      <Footer />
    </React.Fragment>
  );
};

export default Home;
