import React, { useEffect, useState } from "react";
import page_ from "../fonction_js/page_info";
import axios from "axios";

const FaqComponent = ({ scrollPage }) => {
  const [listOfFaq, setListOfFaq] = useState([]);

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}api/faq`)
      .then((response) => {
        setListOfFaq(response.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  useEffect(() => {
    let elements = document.getElementsByClassName("element_container");
    for (let value of elements) {
      page_.scrollFunctionElement(value, "apparitionFaq", true);
    }
  }, [scrollPage]);
  return (
    <div className="faq_container">
      {listOfFaq.map(({ reponse, Question, id }) => (
        <div key={id} className="element_container">
          <div className="question_container">
            <p>{Question}</p>
          </div>
          <div className="response_container">
            <h3>Réponse :</h3>
            <p>{reponse}</p>
          </div>
        </div>
      ))}
    </div>
  );
};

export default FaqComponent;
