import React from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import axios from "axios";

const CreateFaq = ({ setModifyFaqDb, modifyFaqDb }) => {
  const initialValues = {
    Question: "",
    reponse: "",
  };

  const validationSchema = Yup.object().shape({
    Question: Yup.string().required(),
    reponse: Yup.string().required(),
  });
  const OnSubmit = (data, onSubmitProps) => {
    axios
      .post(`${process.env.REACT_APP_API_URL}api/faq`, data)
      .then((response) => {
        console.log("créer", response);
        setModifyFaqDb(!modifyFaqDb);
        onSubmitProps.resetForm();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <React.Fragment>
      <div className="vide"></div>
      <div className="createFaq_container">
        <Formik
          initialValues={initialValues}
          onSubmit={OnSubmit}
          validationSchema={validationSchema}
        >
          <Form className="formContainer">
            <label>Question :</label>
            <ErrorMessage name="Question" component="span" />
            <Field
              className="inputCreatePost"
              name="Question"
              placeholder="Question ..?"
            />
            <label>Réponse :</label>
            <ErrorMessage name="reponse" component="span" />
            <Field
              className="inputCreatePost"
              name="reponse"
              component="textarea"
              rows="6"
              placeholder=" Réponse ...."
            />

            <button className="pointer" type="submit">
              Créer le post
            </button>
          </Form>
        </Formik>
      </div>
    </React.Fragment>
  );
};

export default CreateFaq;
