import React, { useEffect } from "react";
import { membresList } from "../../data/membresList";
import page_ from "../fonction_js/page_info";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faQuoteLeft, faQuoteRight } from "@fortawesome/free-solid-svg-icons";
import Laura from "./../../assets/Laura.jpg";
import Pierrick from "./../../assets/Pierrick.jpg";
import JulesYann from "./../../assets/JulesYann.jpg";

const PresentationMembres = ({ scrollPage, widthScreen, heightScreen }) => {
  useEffect(() => {
    let elements = document.getElementsByClassName("membre_container");
    let i = 1;
    for (let value of elements) {
      if (i % 2 === 1) {
        if (widthScreen < 800) {
          page_.scrollFunctionElement(value, "apparitionMembre", true);
        } else {
          page_.scrollFunctionElement(
            value,
            "apparitionMembre",
            true,
            -heightScreen / 0.7
          );
        }
      }
      i++;
    }
  }, [scrollPage]);
  return (
    <div className="presentation_membres">
      <h2>Présentation des membres de la liste :</h2>
      {membresList.map((value, key) => (
        <div key={key} className="membre_container">
          <div className="description">
            <h2>{value.titre}</h2>
            <FontAwesomeIcon
              icon={faQuoteLeft}
              id="quote1"
              size={"2x"}
              className="faQuote"
            />
            <p>{value.description}</p>
            {/* <FontAwesomeIcon
              icon={faQuoteRight}
              id="quote2"
              size={"2x"}
              className="faQuote"
            /> */}
          </div>
          <React.Fragment>
            {value.coverList.map((member, keysous) => (
              <div key={keysous} className="img">
                <img
                  id={`${member.id}${keysous.toString()}`}
                  alt={`photo de ${member.name}`}
                  src={member.cover}
                />
                <h3>{member.nom}</h3>
                <h4>{member.poste}</h4>
              </div>
            ))}
          </React.Fragment>
        </div>
      ))}
      <div className="membre_container">
        <div className="img">
          <div>
            <img
              // id={`${member.id}${keysous.toString()}`}
              alt={`photo de Laura`}
              src={Laura}
            />
            <h3>Laura</h3>
          </div>
          {/* <h4>{member.poste}</h4> */}
          <div>
            <img
              // id={`${member.id}${keysous.toString()}`}
              alt={`photo de Pierrick`}
              src={Pierrick}
            />
            <h3>Pierrick</h3>
          </div>
          <div>
            <img
              // id={`${member.id}${keysous.toString()}`}
              alt={`photo de `}
              src={JulesYann}
            />
            <h3>Jules-Yann</h3>
          </div>
        </div>
        <div className="description">
          <h2>Pôle entreprise</h2>
          <FontAwesomeIcon
            icon={faQuoteLeft}
            id="quote1"
            size={"2x"}
            className="faQuote"
          />
          <p>
            Notre rôle au pôle entreprise est de favoriser la proximité entre
            les étudiants et les entreprises afin de favoriser l’orientation et
            l’insertion professionnelle des étudiants. En contact direct avec
            les entreprises, nous organisons, en collaboration avec la DRIPE des
            Journées Entreprises et des afterworks.
          </p>
          {/* <FontAwesomeIcon
              icon={faQuoteRight}
              id="quote2"
              size={"2x"}
              className="faQuote"
            /> */}
        </div>
      </div>
    </div>
  );
};

export default PresentationMembres;
